@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #7FFFD4;
    display: flex;
    justify-content: center;
    align-items: center;
}

.App {
    text-align: center;
}

h1 {
    color: #fff;
    margin-bottom: 0.5rem;
    font-size: 1.75rem;
}

.TodoWrapper {
    background: #1A1A40;
    margin-top: 5rem;
    padding: 2rem;
    border-radius: 5px;
}

.TodoForm {
    width: 100%;
    margin-bottom: 1rem;
}

.todo-input {
    outline: none;
    background: none;
    border: 1px solid #8758ff;
    padding: 0.5rem 1rem;
    margin-top: 1rem;
    margin-bottom: 2rem;
    width: 300px;
    color: #fff;
}

.todo-input::placeholder {
    color: #ffffff4d;
}

.todo-btn {
    background: #8758ff;
    color: #fff;
    border: none;
    padding: 0.55rem;
    cursor: pointer;
}

.Todo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #8758ff;
    color: #fff;
    padding: 0.75rem 1rem;
    border-radius: 5px;
    margin-bottom: 1rem;
}

.fa-trash {
    margin-left: 0.75rem;
}

.incompleted {
    color: #fff;
    cursor: pointer;
}

.completed {
    color: #c5aeff;
    text-decoration: line-through;
    cursor: pointer;
}

.edit-icon,
.delete-icon {
    cursor: pointer;
}

@media(max-width: 500px) {
    h1 {
            color: #fff;
            margin-bottom: 0.5rem;
            font-size: 1rem;
        }
        .TodoForm {
                    width: 200px;
                    margin-bottom: 1rem;
                }
        .todo-input {
            width: 140px;
            padding: 0.5rem 1rem;
                margin-top: 0.5rem;
                margin-bottom: 0.5rem;
        }
        .Todo {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: #8758ff;
            padding: 0.25rem 0.5rem;
                border-radius: 2px;
                margin-bottom: 0.5rem;
            color: #fff;
            width: 200px;   
            height: 30px;
     }
        .Todo p {
            font-size: 10px;
        }
        .edit-icon {
            height: 12px;
        }
        .delete-icon {
            height: 12px;
        }
        .todo-input {
            font-size: 8px;
        }
        .todo-btn {
            width: 60px;
            font-size: 8px;
            height: 30px;
        }
}